h Studies")]),_c('p',{staticClass:"pText"},[_vm._v(" Beau Abar, PhD, heads our research team as the Chief Research Officer (CRO). In conjunction with the CEO/CMO and the Program Directors, he leads new study selection, the conduct of the research and its publication. "),_c('br'),_c('br'),_vm._v(" Currently, NARAP is conducting a study on facilitating cervical and breast cancer screenings among emergency department patients and their visitors. Colon-rectal cancer and prostate cancer screening and adverse childhood experiences (ACEs) as a predictor of medical resource utilization are among the future research under consideration."),_c('br'),_c('br'),_vm._v(" NARAP presented its first national, multi-center study, The Emergency Department as a Platform for Tobacco Cessation Utilizing Pre-Health Professional Students as Research Associates at the American College of Emergency Physicians Research Forum at the 2013 annual meeting and is submitted for publication.")]),_c('h5',{staticClass:"headerTwo"},[_vm._v("Research Mode")]),_c('p',{staticClass:"pText"},[_vm._v(" At NARAPs core is a simple, successful research model: pre-health professional students volunteering as Research Associates (RAs) for one 4-hour shift per week as data collectors for clinical studies in the emergency department. RAs facilitate the studies in the manner the investigators choose to use them. Depending on their training, this may range from potential participant identification, through reading the informed consent and scribing for a professional researcher, to data entry and the details concluding the study. Of course, the investigators are responsible for the research; the RAs are there to facilitate."),_c('br'),_c('br'),_vm._v(" NARAP studies on the emergency department as a platform to facilitate primary health care screenings follow this model. During their weekly shifts, RAs approach as many non-emergent patients and their visitors as possible to ask a detailed history on the prevention or screening issue under study. If participants are not up-to-date, RAs offer them ways to correct this, including referrals for specific interventions. After a period of time, other RAs make a telephone follow-up call to determine if the intervention has been accomplished or scheduled."),_c('br'),_c('br'),_vm._v(" RAs carry secure hand-held computer tablets to record data which is transmitted to HIPAA-compliant cloud-based secure web application designed exclusively to support data capture for research studies."),_c('br'),_c('br'),_vm._v(" Often NARAP national, multi-center studies are piloted at a single NARAP institution, then validated in a select group of NARAP hospitals before they are conducted at the national level, as was done for the Tobacco Cessation research.")]),_c('h5',{staticClass:"headerTwo"},[_vm._v("Doing Research With NARAP")]),_c('p',{staticClass:"pText"},[_vm._v(" NARAP is always looking for research projects that would benefit from the high volume and diverse participant enrollment the RA model offers in our multi-center organization. ")]),_c('h5',{staticClass:"headerSmaller"},[_vm._v("Research projects come to NARAP in a number of ways:")]),_c('ul',{staticClass:"list"},[_c('li',[_vm._v("NARAP affiliates")]),_c('li',[_vm._v("Other emergency medicine or other specialty researchers")]),_c('li',[_vm._v("Health related organizations or departments (e.g., government agencies, universities, foundations, non-profits, etc.)")]),_c('li',[_vm._v("Industry (e.g., pharmaceuticals, device developers, demographic data collectors, etc.)")])]),_c('h5',{staticClass:"headerSmaller"},[_vm._v("Criteria for a NARAP study:")]),_c('ul',{staticClass:"list"},[_c('li',[_vm._v("Applicability as a multi-center study, preferably with pilot data")]),_c('li',[_vm._v("Powered to meet the study objectives")]),_c('li',[_vm._v("Meets IRB approval at each participating NARAP site")]),_c('li',[_vm._v("Conducted to the highest ethical and objective standards")]),_c('li',[_vm._v("Prepared for publication regardless of the implications of the results")]),_c('li',[_vm._v("No product promotion")]),_c('li',[_vm._v("Potential to provide meaningful experience for RAs")])]),_c('h5',{staticClass:"headerSmaller"},[_vm._v("Process to propose research project:")]),_c('ol',{staticClass:"list"},[_c('li',[_vm._v("Initial discussion begins with Dr. Keith Bradley, MD at ' "),_c('a',{staticStyle:{"text-decoration":"underline"},attrs:{"href":"mailto:ask@theNARAP.org"}},[_vm._v("ask@theNARAP.org")]),_vm._v(" ' or "),_c('a',{staticStyle:{"text-decoration":"underline"},attrs:{"href":"tel:(203) 767-6363"}},[_vm._v(" (203) 767-6363")])]),_c('li',[_vm._v("If there is mutual interest, the researcher submits a brief outline of the project for review by Dr. Beau Abar, PhD, our Chief Research Officer")]),_c('li',[_vm._v("With his approval, the researcher develops a formal proposal (collaborative work with NARAP on this is encouraged)")]),_c('li',[_vm._v("Next, the proposal is circulated to the NARAP Program Directors for their consensus endorsement to proceed as a NARAP study")]),_c('li',[_vm._v("With the researcher as the Principal Investigator, an Authors Group is developed among Associate Investigators at the PIs institution and NARAP personnel:")]),_c('ul',{staticClass:"sublist"},[_c('li',[_vm._v("Members will be those actively involved in the study to the level of authorship")]),_c('li',[_vm._v("Will act as the steering committee for the study")])]),_c('li',[_vm._v("The study is then given a schedule for IRB approvals, training, enrollment initiation and completion, data analysis, presentations and publication")]),_c('li',[_vm._v("The Authors Group will work closely together to monitor the optimal production of the research")])]),_c('h5',{staticClass:"headerSmaller"},[_vm._v("Cost:")]),_c('p',{staticClass:"pText"},[_vm._v(" NARAP's administrative costs vary on several factors, including the size and complexity of the study, and are based on fair-market value. Funding for NARAP's participation must be included in all federal, foundation, and industry grant applications for studies the organization facilitates. Documentation of funding requests must be submitted with the formal proposal. "),_c('br'),_c('br'),_vm._v(" Our sponsored services are specialized research projects similar to our current healthcare research. Our services are emergency department based, and designed to help you with marketing, strategic planning, proof of concept reports and more. To tell us about your organization and project please contact "),_c('a',{staticStyle:{"text-decoration":"underline"},attrs:{"href":"mailto:ask@theNARAP.org"}},[_vm._v("ask@theNARAP.org")]),_vm._v(". ")])])}]

